export const getGifs = async (categoria) => {

    const url = `https://api.giphy.com/v1/gifs/search?q=${encodeURI(categoria)}&limit=9&api_key=d6nUXW9m3VYQbEiBsRn5P9OOlPJfCzTk`;
    const respuesta = await fetch(url);
    const { data } = await respuesta.json();
    console.log(data);
    const gifs = data.map(img => {
        return {
            id: img.id,
            title: img.title,
            url: img.images.downsized_medium.url
        }
    })
    
    return gifs
}